@use "../abstracts/" as *;
.about {

  
  
  &__text {
    color: var(--color-500);
    flex-direction: column;
    gap: var(--space-sm);
    padding-inline: 1.5rem;
    padding-block: 3rem;

    @include breakpoint(tablet) {
      padding-inline: 3rem;
    }

    @include breakpoint(laptop){
      font-size: var(--fs-sm);
    }
  }

  &__title,
  &__subtitle{
    @include breakpoint(tablet){
      text-align: center;
    }
  }

  &__subtitle{
    @include breakpoint(tablet){
      margin-bottom: var(--space-md);
    }
  }

  &__description p {
    line-height: 1.8;
  }

 

  &__images {
    @include breakpoint(tablet) {
      grid-template-columns: repeat(2, 1fr);
      margin-top: var(--space-lg);
    }

    @include breakpoint(laptop){
      margin-top: var(--space-xl);
    }
  }

  &__img {
    aspect-ratio: 1 / 1;
    object-fit: cover;
  }


  &__btn {
    margin-top: 4.5rem;
    justify-self: center;

    @include breakpoint(tablet){
      margin-top: var(--space-xxl);
    }

    @include breakpoint(laptop){
      margin-top: 9rem;
      margin-bottom: 4rem;
    }
  }
}
