:root {
  --header-height: 5rem;
  --section-spacing: 3rem;
  --space-xs: .5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  --space-xxl: 5rem;
  --margin-xs: 1rem;
  --margin-sm: 1.5rem;
  --margin-md: 2rem;
  --margin-lg: 4rem;
  --margin-xl: 5rem;
  --margin-xxl: 7rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-primary);
  font-size: var(--fs-xs);
  color: var(--color-400);
  background-color: var(--color-100);
  min-height: 100vh;
  line-height: 1.5;
}

* > p + p {
  margin-top: var(--space-xs);
}
@media (min-width: 48rem) {
  * > p + p {
    margin-top: var(--space-sm);
  }
}
@media (min-width: 62.5rem) {
  * > p + p {
    margin-top: var(--space-md);
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  line-height: 1;
}

p {
  line-height: 1.8;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list],
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
}
button svg {
  pointer-events: none;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  /* font-family */
  --ff-primary: "Raleway", sans-serif;
  --ff-secondary: "Comfortaa", cursive;
}

:root {
  /** Colors **/
  --color-600: hsl(0, 0%, 0%);
  --color-500: hsl(30, 9%, 9%);
  --color-400: hsl(30, 7%, 11%);
  --color-300: hsl(0, 0%, 53%);
  --color-200: hsl(0, 0%, 87%);
  --color-100: hsl(0, 0%, 100%);
  --color-accent:#dca47d;
  --color-hover:#d68d59;
}

:root {
  /* font-sizes */
  --fs-biggest: 4.4rem;
  --fs-xxl: 3.2rem;
  --fs-xl: 2.4rem;
  --fs-lg: 2.125rem;
  --fs-md: 1.5rem;
  --fs-sm: 1.25rem;
  --fs-xs: 1rem;
  /* font-weight */
  --fw-300: 300;
  --fw-400: 400;
  --fw-600: 600;
  --fw-700: 700;
  --fw-xbold: 900;
}

.section-title {
  font-size: var(--fs-lg);
  color: var(--color-600);
  text-transform: uppercase;
}
.section-title--white {
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .section-title {
    font-size: var(--fs-xl);
  }
}

.heading-1 {
  color: var(--color-500);
  font-size: var(--fs-md);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: var(--fw-700);
}
.heading-1--white {
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .heading-1 {
    font-size: var(--fs-lg);
  }
}
.heading-2 {
  color: var(--color-500);
  font-size: var(--fs-sm);
}
.heading-2--white {
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .heading-2 {
    font-size: var(--fs-md);
  }
}

.bold {
  font-weight: var(--fw-700);
}

.btn {
  display: inline-block;
  text-transform: uppercase;
  background-color: var(--color-accent);
  letter-spacing: 1px;
  font-weight: var(--fw-700);
  padding: 0.8rem;
  font-size: 14px;
  transition: background-color 0.3s;
  color: var(--color-100);
}
.btn:hover {
  background-color: var(--color-hover);
}
@media (min-width: 48rem) {
  .btn {
    padding: 1rem 1.2rem;
  }
}
@media (min-width: 62.5rem) {
  .btn--xl {
    padding: 1.25rem 1.75rem;
    font-size: var(--fs-sm);
  }
}
.btn--white {
  border: 1px solid white;
  color: var(--color-100);
  background-color: var(--color-600);
}
@media (min-width: 62.5rem) {
  .btn--white {
    padding: 0.5rem;
  }
}

.scroll-top {
  position: fixed;
  background-color: var(--color-accent);
  right: 1rem;
  bottom: -20%;
  padding: 0.5rem;
  display: flex;
  z-index: 10;
  transition: bottom 0.6s;
  border-radius: 3px;
  opacity: 0.8;
  transition: bottom 0.5s, opacity 0.3s;
}
.scroll-top:hover {
  opacity: 1;
}
.scroll-top.show-top-btn {
  bottom: 2rem;
}
@media (min-width: 62.5rem) {
  .scroll-top.show-top-btn {
    bottom: 3rem;
  }
}
@media (min-width: 62.5rem) {
  .scroll-top {
    right: 3rem;
    padding: 0.7rem;
  }
}

.card__container {
  position: relative;
}
.card__img {
  aspect-ratio: 1/1;
  object-fit: cover;
}
.card__description {
  padding: 0.5rem 0;
  display: grid;
}
.card__title {
  font-size: var(--fs-500);
  font-weight: var(--fw-300);
  line-height: 1.2;
  align-self: start;
}
.card__country {
  margin-bottom: 0.5rem;
}
.card__price {
  font-weight: var(--fw-700);
  font-size: var(--fs-400);
  align-self: end;
}
.card__btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
  transition: stroke 0.4s;
}
.card__btn svg {
  pointer-events: none;
}

.cart-btn {
  position: relative;
  cursor: pointer;
}
@media (min-width: 62.5rem) {
  .cart-btn {
    margin-bottom: 6px;
  }
}
.cart-btn svg {
  position: relative;
  pointer-events: none;
  stroke: white;
}
@media (min-width: 62.5rem) {
  .cart-btn svg {
    stroke: black;
  }
}

.cart-btn-quantity {
  background-color: var(--color-accent);
  border-radius: 40%;
  color: var(--color-100);
  padding: 0px 4px;
  position: absolute;
  right: -7px;
  top: -2px;
  font-size: 14px;
}

.header {
  position: relative;
  padding-bottom: 1rem;
  background-color: var(--color-100);
  width: 100%;
  z-index: 99;
  margin: 0 auto;
  transition: transform 0.5s, padding 0.5s;
}
@media (min-width: 87.5rem) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
  }
  .header.scroll-down {
    transform: translate3d(0, -100%, 0);
  }
  .header.scroll-up {
    box-shadow: 0 -5px 20px rgba(0, 0, 0, 0.1);
    padding-top: 0;
  }
}

.top-bar {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
  transition: transform 0.4s;
}
@media (min-width: 62.5rem) {
  .top-bar {
    align-items: flex-end;
  }
}
.top-bar__logo {
  width: 130px;
}
@media (min-width: 48rem) {
  .top-bar__logo {
    width: 160px;
  }
}

.main-nav {
  background-color: var(--color-500);
  color: var(--color-100);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 1.5rem;
  transform: translateY(-50%);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s, transform 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5rem;
}
@media (min-width: 62.5rem) {
  .main-nav {
    background-color: transparent;
    color: var(--color-500);
    position: initial;
    width: initial;
    height: initial;
    padding: initial;
    z-index: initial;
    transform: initial;
    transition: initial;
    opacity: initial;
    visibility: initial;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}
@media (min-width: 48rem) {
  .main-nav__list {
    gap: var(--space-md);
  }
}
@media (min-width: 62.5rem) {
  .main-nav__list {
    flex-direction: row;
    align-items: center;
  }
}
.main-nav__item {
  position: relative;
  display: flex;
  padding: 0.25rem 0;
}
.main-nav__link {
  font-size: var(--fs-md);
  letter-spacing: 1.5px;
  position: relative;
  text-transform: uppercase;
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .main-nav__link {
    font-size: var(--fs-md);
  }
}
@media (min-width: 62.5rem) {
  .main-nav__link {
    font-size: var(--fs-sm);
    font-weight: 700;
    color: var(--color-500);
  }
}
.main-nav__link:after, .main-nav__link.current:after {
  content: "";
  position: absolute;
  top: 50%;
  right: -1rem;
  transform: translateY(-50%);
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: var(--color-accent);
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 62.5rem) {
  .main-nav__link:after, .main-nav__link.current:after {
    top: initial;
    right: initial;
    bottom: -0.25rem;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-nav__link.current:after {
  opacity: 1 !important;
}
.main-nav__link:hover::after {
  opacity: 1;
}
.main-nav.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.main-nav__social-list {
  gap: 1.25rem;
}

.menu-btn {
  position: relative;
  width: 1.8rem;
  height: 1.125rem;
  background: transparent;
  z-index: 20;
  transition: transform 0.4s;
}
.menu-btn span {
  position: absolute;
  display: block;
  right: 0.1rem;
  width: 1.8rem;
  height: 3px;
  background: #000;
  transition: top 0.4s 0.4s, opacity 0.3s 0s, transform 0.4s 0s, background-color 0.4s;
  pointer-events: none;
}
.menu-btn span:nth-child(1) {
  top: 0;
}
.menu-btn span:nth-child(2) {
  top: calc(50% - 1px);
}
.menu-btn span:nth-child(3) {
  top: calc(100% - 2px);
}
.menu-btn.active span {
  transition: top 0.4s 0s, opacity 0.3s 0s, transform 0.4s 0.4s, background-color 0.4s;
  background-color: #fff;
}
.menu-btn.active span:nth-child(1) {
  top: calc(50% - 1px);
  transform: rotate(45deg);
}
.menu-btn.active span:nth-child(2) {
  opacity: 0;
}
.menu-btn.active span:nth-child(3) {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

