@media (min-width: 87.5rem) {
  .products {
    margin-top: 10rem;
  }
}
@media (min-width: 87.5rem) {
  .products {
    margin-top: 11rem;
  }
}
.products__header {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}
@media (min-width: 62.5rem) {
  .products__header {
    flex-direction: row;
    gap: 3rem;
  }
}
.products__container {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  position: relative;
}
@media (min-width: 62.5rem) {
  .products__container {
    gap: 4rem;
    padding: 6rem 0;
  }
}

.product__img-container {
  margin-top: var(--space-xl);
  position: relative;
  overflow: hidden;
}
.product__img-container:hover .product__add-btn {
  transform: translateX(0);
}
.product__img-container:hover .product__img {
  opacity: 0.5;
}
.product__img {
  transition: opacity 0.4s;
  aspect-ratio: 1/1;
  object-fit: cover;
  cursor: pointer;
}
.product__add-btn {
  position: absolute;
  bottom: 15%;
  right: 0;
  background-color: var(--color-accent);
  font-weight: bold;
  font-size: 12px;
  padding: 0.5rem 0.75rem;
  text-transform: uppercase;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  transform: translateX(100%);
  transition: transform 0.6s;
}
.product__add-btn span {
  pointer-events: none;
}
.product__add-btn:hover span {
  color: white;
}
.product__add-btn:hover svg {
  stroke: white;
}
.product__info {
  margin-top: var(--space-sm);
  display: grid;
}
@media (min-width: 48rem) {
  .product__info {
    grid-template-rows: auto 125px auto;
  }
}
.product__info h4 {
  color: var(--color-accent);
}
.product__name {
  font-size: var(--fs-md);
  margin-top: var(--space-xs);
}
.product__description {
  color: var(--color-300);
  margin-top: var(--space-xs);
  margin-bottom: var(--space-xs);
}
.product__price {
  font-size: var(--fs-md);
}

.cart {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: var(--color-100);
  z-index: 9999;
  padding: 1.5rem;
  transform: translateX(100%);
  transition: transform 0.4s;
  opacity: 1;
}
@media (min-width: 48rem) {
  .cart {
    width: 30vw;
    min-width: 450px;
  }
}
.cart.show-cart {
  transform: translateX(0);
}
.cart__close-btn {
  margin: 0;
  padding: 0;
}
.cart__close-btn svg {
  width: 2.5rem;
  height: 2.5rem;
  stroke-width: 1.2;
}
.cart__title {
  margin-top: var(--space-lg);
  margin-bottom: var(--space-xl);
}

.cart__items {
  display: grid;
  gap: 3rem;
}

.cart__item {
  align-items: center;
  grid-template-columns: auto 1fr auto;
  gap: 1.5rem;
}
.cart__item-img {
  width: 80px;
  aspect-ratio: 1/1;
  object-fit: cover;
}
.cart__item-title {
  line-height: 1.1;
}
.cart__item-price {
  margin-top: var(--space-xs);
}
.cart__item-remove-btn {
  color: var(--color-300);
  font-size: 14px;
}
.cart__item-quantity-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cart__item-plus-btn, .cart__item-minus-btn {
  display: grid;
  place-content: center;
}
.cart__item-plus-btn svg, .cart__item-minus-btn svg {
  stroke: var(--color-accent);
  stroke-width: 2px;
}

.cart__footer {
  margin-top: var(--space-xl);
}
.cart__footer h3 {
  margin-bottom: var(--space-sm);
}
.cart__footer-clear-btn {
  margin-top: var(--space-lg);
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 48rem) {
  .container {
    padding: 0 3rem;
  }
}

.container-narrow {
  margin: 0 auto;
  max-width: 37rem;
  padding-inline: 1.5rem;
}
@media (min-width: 62.5rem) {
  .container-narrow {
    max-width: 48rem;
  }
}

.container-md {
  margin: 0 auto;
  max-width: 60rem;
  padding-inline: 1.5rem;
}
@media (min-width: 48rem) {
  .container-md {
    padding: 0 3rem;
  }
}

.grid {
  display: grid;
}

.flex {
  display: flex;
}
.flex--sb {
  justify-content: space-between;
  align-items: center;
}
.flex--center {
  justify-content: center;
  align-items: center;
}

.border {
  border: 1px solid green;
}

.section {
  padding-top: 1.5rem;
  padding-bottom: 3rem;
}
@media (min-width: 48rem) {
  .section {
    padding-top: 3rem;
    padding-bottom: 5rem;
  }
}

@media (min-width: 48rem) {
  .reverse {
    order: 1;
  }
}

.no-scroll {
  position: fixed;
  overflow-y: scroll;
  width: 100%;
}

@media (min-width: 62.5rem) {
  .md-screen-hidden {
    display: none;
  }
}

@media (min-width: 87.5rem) {
  .lg-screen-hidden {
    display: none;
  }
}

body.stop-transition .main-nav {
  transition: none !important;
}

@media (min-width: 62.5rem) {
  .article-container {
    margin-inline: auto;
    max-width: 1200px;
  }
}

.spot-icon {
  width: 100vw;
  background-image: url("../../../dist/images/coffee-bean.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 3rem;
  height: 3rem;
}
@media (min-width: 62.5rem) {
  .spot-icon {
    padding-block: 7rem;
    height: 5rem;
    background-size: 5rem;
  }
}

.span-line {
  max-width: 21ch;
  line-height: 1.2;
  font-weight: var(--fw-300);
  font-size: var(--fs-sm);
}
@media (min-width: 62.5rem) {
  .span-line {
    grid-column: 2/3;
    justify-self: center;
    font-size: var(--fs-md);
  }
}